tur',sans-serif;letter-spacing:.5px;text-decoration:none;transition:.3s ease-out;cursor:pointer}a:hover{text-decoration:none;color:#008ae6;transition:.3s ease-out}a::after{content:'';position:absolute;display:block;bottom:0;left:50%;width:0;height:2px;border-bottom:2px solid #008ae6}a,a::after{transition:.15s linear}.container-fluid{line-height:24px}.container-flex-col{display:flex;flex-direction:column;justify-content:center;align-items:center;height:300px;margin-top:calc(50vh - 45px - 150px)}header{position:relative;display:block;width:100%;color:#fff;height:90px;font-weight:700;z-index:10}header .logo{position:absolute;top:50%;left:15px;transform:translateY(-50%)}header .logo img{height:50px}header nav,header nav ul,header nav ul li{margin:0;padding:0;margin-left:24px}header nav{position:absolute;top:50%;right:15px;transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-top:5px;margin-right:10px}header nav ul li a{position:relative;display:inline-block;font-size:16px;line-height:24px;height:24px;text-decoration:none;color:#010000}footer{position:absolute;bottom:0;display:block;width:100%;color:#010000;height:90px;z-index:10}footer .footerText{position:absolute;top:85%;left:45%;font-size:12px;text-align:center;transform:translateY(-50%)}
